import React, { useEffect, useState, useRef } from "react";
import { PrimaryButton, TextField } from "office-ui-fabric-react/lib/index";
import "./style.css";
import { navigate } from "@reach/router";
import axios from "axios";
import config from "../../config";
import { inIframe } from "../../helpers";
import {
  signinWith,setReturnPath
} from "../../authenticate";
import Wrapper from "../Wrapper";
export default function HomePage() {
  const [user, setUser] = useState(null);
  const [errorMessage, setErrorMessage] = useState(null);
  const [email, setEmail] = useState("");
  const [isProcessing,setIsProcessing] = useState(false);
  const mailInput = useRef(null);

  useEffect(()=>{
     // current property is refered to input element
     if (mailInput.current) {mailInput.current.focus();}
  },[])

  const validateEmail = (email) => {


      // don't remember from where i copied this code, but this works.
      let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

      if ( re.test(email) ) {
        setErrorMessage(null)
          // this is a valid email address
          // call setState({email: email}) to update the email
          // or update the data in redux store.
      }
      else {
        setErrorMessage("Enter a valid email address")
          // invalid email, maybe show an error to the user.
      }
  }
  const processEmail = () => {
    setErrorMessage(null);
    setIsProcessing(true)

    var data = JSON.stringify({ email });

    var config = {
      method: "post",
      url:
        "https://prod-44.northeurope.logic.azure.com:443/workflows/7de38d3eaea84179800d7ec6da48cef8/triggers/manual/paths/invoke?api-version=2016-10-01&sp=%2Ftriggers%2Fmanual%2Frun&sv=1.0&sig=mcDwAJFptdj3ZVSdDWVpGtch-sVvMZpOlB5Dv8QMXxI",
      headers: {
        "Content-Type": "application/json",
      },
      data: data,
    };
    axios(config)
      .then(function (response) {
     

        switch (response.status) {
          case "value":
            break;

          default:
            break;
        }
        setReturnPath(response.data.url)
        signinWith(email)
       // setIsProcessing(false)

                
      })
      .catch(function (error) {
        setIsProcessing(false)

        setErrorMessage(error.message);
        
      });
  };

  return (
  <Wrapper>
 
        {/* <div style={{ width: "100%", padding: "30px" }}>
          SpeedDating is using Microsoft Teams for hosting "Dates". 10 minutes
          after you click "Meet Now" you will be able to join an Ad Hoc meeting
          other members of the community. Each relevant members get's an email
          &amp; a notification in Teams allowing them to join with One Click.
          They can click or delete the mail, nothing is left behind in their
          calendar
        </div> */}
        <div className="speedDateActions">
          
          <TextField
          className="email"
          componentRef={mailInput}
           type="email"
           placeholder="Enter your login email addresss"
            required
            errorMessage={errorMessage}
            onChange={(a, b) => {
              setEmail(b);
              validateEmail(b)
            }}
          />
          <div style={{ padding: "10px" }}>
            <PrimaryButton className="signin"
            disabled={isProcessing || errorMessage || email.length === 0}
              onClick={() => {
                processEmail();
              }}
            >
              Continue
            </PrimaryButton>
          </div>
          
        </div>
        </Wrapper>
  );
}
